import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { Card, ResponsiveEmbed, Container } from "react-bootstrap";

const Waytocome = () => (
  <Layout>
    <SEO title="Waytocome" />
    <Card>
      <Card.Body>
        <ResponsiveEmbed aspectRatio="16by9">
          <embed type="image/svg+xml" src="http://kko.to/LuKnlb_DM" />
        </ResponsiveEmbed>
      </Card.Body>
      <Card.Footer className="bg-white">
        <small className="text-muted">삼막골카라반펜션</small>
        <Card.Text>경상북도 경주시 갈곡삼막길 43-115</Card.Text>
      </Card.Footer>
    </Card>
    <Container fluid>
      <div class="my-4 pt-4">
        <Link to="https://youtu.be/2AFmWJTntEs" style={{textDecoration: `none`, }}>
        <h5>오시는 길 - 동영상으로 보기</h5>
        </Link>
      </div>
      <hr />
      <div class="my-4 pt-4">
        <h5>자동차</h5>
        <h6>경상북도 포항시 남구 송도동 송도로 62</h6>
        <h6>경상북도 포항시 남구 송도동 송도로 62</h6>
      </div>
      <hr />
      <div class="my-4 pt-4">
        <h5>버스</h5>
        <h6>경상북도 포항시 남구 송도동 송도로 62</h6>
        <h6>경상북도 포항시 남구 송도동 송도로 62</h6>
      </div>
      <hr />
    </Container>
  </Layout>
)

export default Waytocome